import styled from 'styled-components';

export const NavbarWrapper = styled.div`
  width: 100%;
  height: 50px;
  color: aliceblue;
  position: ${({ isSticky }) => (isSticky ? 'fixed' : 'absolute')};
  top: ${({ isSticky }) => (isSticky ? '0px' : '50px')};
  z-index: 1000;
  transition: top 0.3s, opacity 0.3s; /* Adiciona uma transio de 0.3 segundos para top e opacity */
  background-color: rgba(64, 45, 10, 0.4); /* Cor de fundo com transparncia */
  box-shadow: ${({ isSticky }) => (isSticky ? '0 2px 4px rgba(0, 0, 0, 0.9)' : 'none')};
  opacity: ${({ isSticky }) => (isSticky ? '1' : '0')}; /* Define a opacidade como 1 quando isSticky for verdadeiro */
  backdrop-filter: blur(10px); /* Adiciona um efeito de borro ao fundo */

  @media (max-width: 768px) {
    position: fixed; /* Mantm a posio fixa em telas menores */
    top: ${({ isSticky }) => (isSticky ? ' 0px' : '50px')}; /* Ajusta a posio do topo apenas quando fixo */
    height: 50px; /* Altura da barra de navegao em telas menores */
  }
`;

