import React, { useState, useEffect } from 'react';
import { NavbarWrapper, Menu, MenuItem } from './baraStyled';

function Navbar() {
  const [isSticky, setIsSticky] = useState(false);
  const [activeSection, setActiveSection] = useState('');

  useEffect(() => {
    const handleScroll = () => {
      const offset = window.scrollY;
      setIsSticky(offset > 10);

      // Verifica qual seo est na rea visvel da tela
      const sections = document.querySelectorAll('section'); // Supondo que as sees tenham a tag <section>
      sections.forEach((section) => {
        const rect = section.getBoundingClientRect();
        if (rect.top <= window.innerHeight / 2 && rect.bottom >= window.innerHeight / 2) {
          setActiveSection(section.id);
        }
      });
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const scrollToSection = (e, sectionId) => {
    e.preventDefault();
    const section = document.getElementById(sectionId);
    const offset = 100; // Valor de deslocamento ajustvel
    const topPos = section.getBoundingClientRect().top + window.pageYOffset - offset;
    window.scrollTo({ top: topPos, behavior: 'smooth' });
  };
  

  return (
    <NavbarWrapper isSticky={isSticky}>
      <Menu>
        <MenuItem active={activeSection === 'local'}><a href="#local" onClick={(e) => scrollToSection(e, 'local')}>Local</a></MenuItem>
        <MenuItem active={activeSection === 'sobre'}><a href="#sobre" onClick={(e) => scrollToSection(e, 'sobre')}>Sobre</a></MenuItem>
        <MenuItem active={activeSection === 'servicos'}><a href="#servicos" onClick={(e) => scrollToSection(e, 'servicos')}>Servios</a></MenuItem>
       </Menu>
    </NavbarWrapper>
  );
}

export default Navbar;
